@use 'base/variables' as v;
@use 'base/mixins' as m;

.aboutus-content{
    @include m.mediaQuery(v.$tablet){
        @include m.grid(2, 2rem);
    }
}

.aboutus-text blockquote{
    font-weight: v.$weightBlack;
    font-size: 2.2rem;
    margin: 3rem 0 0 0;
    padding-left: 1rem 0 3rem 0;
}