  display: none;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.center-content {
  max-width: 80rem;
}

.align-right {
  display: flex;
  justify-content: flex-end;
}

.button-yellow {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 10px;
}
@media (min-width: 1024px) {
  .button-yellow:hover {
    background-color: #c77808;
    cursor: pointer;
  }
}

.button-yellow-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 10px;
}
@media (min-width: 1024px) {
  .button-yellow-block:hover {
    background-color: #c77808;
    cursor: pointer;
  }
}

.button-green {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 10px;
}
@media (min-width: 1024px) {
  .button-green:hover {
    background-color: #619800;
    cursor: pointer;
  }
}

.dark {
  background-color: #262626;
}
.dark p {
  color: #FFFFFF;
}
.dark a,
.dark h1,
.dark h2,
.dark h3,
.dark h4,
.dark blockquote {
  color: #e1e1e1;
}
.dark .for-sale {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark .for-sale .price {
  color: #71B100;
}
.dark .for-sale .icon {
  filter: invert(100%);
}
.dark .property-details .for-sale-price {
  color: #71B100;
}
.dark .property-details .icon {
  filter: invert(100%);
}
.dark .form label,
.dark .form legend,
.dark .form p {
  color: #FFFFFF;
}
.dark .form input:not([type=submit]),
.dark .form textarea,
.dark .form select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}

.no-webp .index {
  background-image: url(../img/header.jpg);
}

.webp .index {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.index {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.index {
    min-height: 50rem;
  }
}
@media (min-width: 768px) {
  .header.index .header-content {
    min-height: 50rem;
  }
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-content h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
  padding-left: 2.5rem;
}
@media (min-width: 768px) {
  .header-content h1 {
    padding-left: 1.6rem;
  }
}

.section h1 {
  font-weight: 700;
}

.footer {
  padding: 1rem 2.5rem;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .footer-container {
  padding-top: 2.5rem;
}
@media (min-width: 768px) {
  .footer .footer-container {
    padding-top: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  min-width: fit-content;
}

.bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
.bar img {
  width: 40rem;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
  }
  .bar img {
    width: 30rem;
  }
}

.dark-mode {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
@media (min-width: 768px) {
  .dark-mode {
    padding: 1rem 0 1rem 2rem;
  }
}
.dark-mode .dark-mode-btn {
  width: 5rem;
  filter: invert(100%);
}
@media (min-width: 768px) {
  .dark-mode .dark-mode-btn {
    width: 3rem;
  }
  .dark-mode .dark-mode-btn:hover {
    cursor: pointer;
  }
}

.right {
  width: 100%;
  height: 5rem;
}
@media (min-width: 768px) {
  .right {
    width: auto;
    display: flex;
    flex-direction: row-reverse;
  }
}

.navigation {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.1s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navigation {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
  }
}
.navigation a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navigation a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navigation a:last-of-type {
    margin-right: 0;
  }
}

.mobile-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.nav-footer {
  display: block;
  visibility: visible;
  opacity: 1;
}
@media (min-width: 768px) {
  .nav-footer {
    display: flex;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .about-us-icons {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
  font-weight: 700;
}

.for-sale-section h2 {
  font-weight: 700;
}

@media (min-width: 768px) {
  .for-sale-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.for-sale {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  border-radius: 10px;
  margin-bottom: 2rem;
}
.for-sale img {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.for-sale .for-sale-content {
  padding: 2rem;
}
.for-sale h3,
.for-sale p {
  margin: 0;
}

.show-all a {
  width: 100%;
}
@media (min-width: 768px) {
  .show-all a {
    width: auto;
  }
}

.for-sale-price {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.icons-info {
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 0;
}
.icons-info li {
  flex: 1;
  display: flex;
  justify-content: center;
}
.icons-info li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.icons-info li p {
  font-weight: 900;
}
.icons-info.detailed {
  max-width: 40rem;
}
.icons-info.detailed li {
  justify-content: flex-start;
}

.no-webp .contact-image {
  background-image: url("../img/encuentra.jpg");
}

.webp .contact-image {
  background-image: url("../img/encuentra.webp");
}

.contact-image {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact-image h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.contact-image p {
  font-size: 1.8rem;
  color: #FFFFFF;
  width: 95%;
}
@media (min-width: 768px) {
  .contact-image p {
    width: fit-content;
  }
}

.blog h3 {
  font-weight: 700;
}

@media (min-width: 768px) {
  .bottom-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.blog-post {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .blog-post {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.blog-post:last-of-type {
  margin-bottom: 0;
}
.blog-post img {
  height: 100%;
}

.meta-info span {
  color: #E08709;
}

.blog-text a {
  color: #000000;
}
.blog-text h4 {
  margin: 2rem 0 0 0;
  line-height: 1.4;
}
@media (min-width: 768px) {
  .blog-text h4 {
    margin-top: 0;
  }
}
.blog-text h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 0.5rem;
}

.feedback {
  background-color: #71B100;
  font-size: 2.2rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.feedback blockquote {
  padding-left: 5rem;
  position: relative;
}
.feedback blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.feedback p {
  text-align: right;
  color: #FFFFFF;
  font-weight: 700;
}

.form fieldset {
  border-radius: 2rem;
}
.form p {
  font-size: 1.8rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form textarea {
  min-width: 100%;
  min-height: 10rem;
  max-height: 15rem;
}
.form .contact-by {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}

/* body {
    background-color: black;
    color: white;

    legend {
        color: white;
    }
} */
@media (min-width: 768px) {
  .aboutus-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.aboutus-text blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}